const style = step => {
  return {
    "background-color": step == i + 1 ? "blue" : "grey"
  };
};

const onStyle = {
  "background-color": "blue"
};

const steps = step => {
  const steps = [...Array(8)];
  return steps.map((_, i) => {
    return <div className="column" style={step == i + 1 ? onStyle : style} />;
  });
};

const Track = props => <div className="columns">{steps(props.step)}</div>;

export default Track;
